#fan {
  margin: 100px auto 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 500px;
}

#head {
  position: relative;
  height: 60%;
  aspect-ratio: 1 / 1;
  border: 5px solid grey;
  border-radius: 100%;
  overflow: hidden;
  transform: translateY(2px);
}

#grille {
  position: absolute;
  left: -106%;
  height: 500%;
  width: 500%;
  background-image:
    repeating-radial-gradient(grey 0px, grey 2px, transparent 2.5px, transparent 15px, grey 15.5px), repeating-linear-gradient(45deg, grey 0px, grey 2px, transparent 2.5px, transparent 45px, grey 45.5px);
  transform-origin: top left;
  transform: rotate(-27.5deg);
}

#blades {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 70%;
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  z-index: -1;
  animation: spin .5s linear infinite;
  transition: rotate 4s ease-out;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {  
    transform: rotate(360deg)
  }
}

.off {
  rotate: 0deg;
  animation-play-state: paused !important;
}

.on {
  rotate: -1440deg;
  animation-play-state: running !important;
}

.blade {
  height: 45%;
  width: 45%;
  background-color: aquamarine;
}

.blade:nth-child(n+1):nth-child(-n+4) {
  border-radius: 2px 100%;
}

.blade:nth-child(n+2):nth-child(-n+3) {
  border-radius: 100% 2px;
}

#center {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 10%;
  width: 10%;
  border-radius: 100%;
  background-color: darkgray;
  z-index: -1;
}

#stand {
  height: 35%;
  aspect-ratio: 1 / 6;
  background-color: grey;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#stand button {
  width: 75%;
  aspect-ratio: 1 / 1.25;
  border-radius: 5px;
  background-color: aquamarine;
  cursor: pointer;
}

#base {
  height: 5%;
  aspect-ratio: 8 / 1;
  background-color: grey;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
